<template>
    <div class="login">
        <div class="signin">
            <div style="width:138px;margin:0px auto;padding-bottom:20px;">
                <svg version="1.1" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" x="0px"
                    y="0px" viewBox="0 0 200 66.92" style="enable-background: new 0 0 200 66.92;" xml:space="preserve">
                    <image style="overflow: visible;" width="1772" height="506"
                        transform="matrix(0.1071 0 0 0.1071 5.1307 5.6736)" xlink:href="@/assets/Definer_logo.png"></image>
                </svg>
            </div>
            <p v-if="!hideList" style="color:black;padding-bottom:20px;">Edit design preference for project {{
                preferenceNameValue }}</p>
            <div class="section" style=" margin-left: 20px; margin-right:18px;" v-if="!hideList">
                <div class="section-title" style="margin-bottom:10px;">
                    Preference name
                </div>
                <div class="section-content" style="margin-bottom:20px;">
                    <el-input type="text" v-model="preferenceNameValue"
                        style="border: none; box-shadow: none; background-color: transparent; padding: 0;"></el-input>
                </div>

            </div>
            <div style="margin-top:20px;" class="list_item" v-if="!hideList">
                <div class="box" v-for="item in list" :key="item.id" :class="{ 'selected': item.selected }">
                    <div class="main" @click="toggleSelection(item)">
                        <div class="cover" v-loading="item.loading" :element-loading-text="lang.loading + '...'">
                            <img style="width:170px;height:150px;object-fit:cover;" class="project_cover"
                                :src="'https://www.simplerevise.com/img/project/testimg/thumbnail/' + item.img_url + '.jpg'"
                                alt="">
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="showSelectedImages">
                <p style="color:black;padding-bottom:20px;font-weight:700;font-size:26px;">Review your selection</p>
                <div style="display: flex; justify-content: center;">
                    <p v-if="!editingPreferenceName"
                        style="color:black;padding-bottom:10px;font-weight:500;font-size:23px;">
                        {{ reviewPreferenceName }}
                    </p>
                    <div style="display: flex; justify-content: center;" v-else>
                        <el-input v-model="editedPreferenceName" placeholder="Enter preference name"></el-input> &nbsp;
                        &nbsp;
                        <el-button @click="savePreferenceName">Save</el-button>
                    </div>
                    <svg v-if="!editingPreferenceName" class="edit-icon" @click="startEditingPreferenceName"
                        xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="currentColor"
                        stroke-width="2" stroke-linecap="round" stroke-linejoin="round"
                        style="width: 24px; height: 24px; margin-left: 5px; cursor: pointer;">
                        <path d="M12 20h9"></path>
                        <path d="M4 20h4"></path>
                        <path d="M14.12 3.29L17 6.17L6.88 16.29L3 12.41V9.5H6.91L14.12 3.29Z"></path>
                    </svg>
                </div>
                <el-form-item v-if="editingPreferenceName">
                    <div style="display: flex; justify-content: center;">
                        <el-input v-model="editedPreferenceName" placeholder="Enter preference name"></el-input>
                        <el-button @click="savePreferenceName">Save</el-button>
                    </div>
                </el-form-item>
                <div style="margin-top:20px;" class="list_item">
                    <div class="box" v-for="item in list" v-if="item.selected" :key="item.id"
                        :class="{ 'selected': item.selected }">
                        <div class="main">
                            <div class="cover" v-loading="item.loading" :element-loading-text="lang.loading + '...'">
                                <img style="width:170px;height:150px;object-fit:cover;" class="project_cover"
                                    :src="'https://www.simplerevise.com/img/project/testimg/thumbnail/' + item.img_url + '.jpg'"
                                    alt="">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div style="display: flex; justify-content: center; margin-top: 20px;">
                <el-button v-if="showSelectedImages" type="primary" class="submit" :loading="submitting" @click="change()"
                    style="width: 100px;">
                    Change
                </el-button>
                <el-button v-else type="primary" class="submit" :loading="submitting" @click="goBack()"
                    style="width: 80px;">
                    Back
                </el-button>
                <el-button v-if="showSelectedImages" type="primary" class="submit" :loading="submitting" @click="save()"
                    style="width: 80px;">
                    Save
                </el-button>
                <el-button v-else type="primary" class="submit" :loading="submitting" @click="next()" style="width: 80px;">
                    Next
                </el-button>
            </div>
        </div>
    </div>
</template>
  
<script>
import timer from '../tools/timer';
import global from '@/tools/global';
export default {
    data() {
        return {
            list: [],
            selectedOrganisation: '',
            selectedDesignType: '',
            selectedPreference: '',
            showSelectedImages: false,
            hideList: false,
            preferenceName: '',
            reviewPreferenceName: 'Your Preference Name',
            editingPreferenceName: false,
            editedPreferenceName: '',
            preferenceNameValue: '', // 
            projectId: null,
            loading: false,
            organisations: [
                { id: 1, name: 'Org 1' },
                { id: 2, name: 'Org 2' },
                { id: 3, name: 'Org 3' }
            ],
            designTypes: [
                { id: 1, name: 'Design 1' },
                { id: 2, name: 'Design 2' },
                { id: 3, name: 'Design 3' }
            ],
            preferences: [
                { id: 1, name: 'Preference 1' },
                { id: 2, name: 'Preference 2' },
                { id: 3, name: 'Preference 3' }
            ]
        };
    },
    created() {
        this.lang = this.$lang('board');

        this.getList();
    },
    methods: {
        getList() {
            this.loading = true;
            const groupId = this.$route.query.group_id;
            this.$ajax.get('/project/list_u_selected_library/?group_id=' + this.$route.query.group_id).then(r => {
                // Process the retrieved data
                this.list = r.map(item => {
                    item.create_time = timer.time('y-m-d', item.create_time * 1000);
                    item.loading = true;
                    item.selected = 0;
                    if (item.board_selected === '1') {
                        this.toggleSelection(item);
                    }

                    return item;
                });

                // Extract board_id from each item and store in an array
                const boardIds = this.list.map(item => item.board_id);


                if (this.list.length > 0) {
                    this.preferenceNameValue = this.list[0].preference_name;
                }

                // 处理封面展示方式，横图 = row，竖图 = col
                this.$nextTick(() => {
                    let imgs = document.querySelectorAll('.project_cover');
                    imgs.forEach((item, index) => {
                        let img = new Image();
                        img.onload = () => {
                            this.list[index].loading = false;
                            if (img.width > img.height) {
                                item.classList.add('row');
                            } else {
                                item.classList.add('col');
                            }
                        }
                        img.src = item.src;
                    })
                    this.loading = false;
                })

                // Use the boardIds array as needed
                console.log(boardIds[0]);

            })
                .catch(error => {
                    console.error(error);
                    this.loading = false; // Set loading to false in case of an error
                });
        },
        toggleSelection(item) {
            if (this.countSelectedImages() >= 8 && !item.selected) {
                // Limit the selection to a maximum of 5 images
                return;
            }
            item.selected = !item.selected;
            this.$forceUpdate();
        },

        countSelectedImages() {
            return this.list.filter(item => item.selected).length;
        },
        handleUpload(event) {
            // Handle the file upload logic here
        },
        next() {

            if (this.preferenceNameValue.trim() === '') {
                alert('Preference name cannot be empty');
                return; // Stop execution if the field is empty
            }

            // Check if at least three images are selected
            const selectedImages = this.list.filter(item => item.selected);
            if (selectedImages.length < 3) {
                alert('Select at least three images');
                return; // Stop execution if less than three images are selected
            }

            // Proceed to the next step
            this.showSelectedImages = true;
            this.hideList = true;
            this.reviewPreferenceName = this.preferenceNameValue;
        },
        change() {
            this.showSelectedImages = false;
            this.hideList = false;
        },
        startEditingPreferenceName() {
            this.editingPreferenceName = true;
            this.editedPreferenceName = this.reviewPreferenceName;
        },
        savePreferenceName() {
            if (this.editedPreferenceName.trim() !== '') {
                this.reviewPreferenceName = this.editedPreferenceName;
                this.editingPreferenceName = false;
            }
        },

        save() {
            const projectId = this.projectId; // Access the project ID from the component's data
            const selectedImages = this.list.filter(item => item.selected).map(item => item.img_url);

            if (this.reviewPreferenceName.trim() === '') {
                alert('Preference name cannot be empty');
                return;
            }

            if (selectedImages.length < 3) {
                alert('Select at least three images');
                return;
            }

            const requestData = {
                project_id: projectId,
                images: selectedImages, // Add the selected image URLs to the request data
                name: this.reviewPreferenceName,
                groupId: this.$route.query.group_id,
            };

            this.$ajax
                .post(`/project/update_preference/`, requestData)
                .then(response => {
                    // Handle the response from the server
                    this.getList(response);
                    this.$message.success('Success');
                    // this.$router.push(`/done?id=${projectId}`);
                    this.$router.push(`/done?id=${this.list[0].board_id}`);
                })
                .catch(error => {
                    // Handle any errors that occur during the request
                    console.error(error);
                    this.$message.error('Error occurred');
                });
        },
        goBack() {
            this.$router.go(-1);
        },
        getDesignPreferenceLink() {
            return {
                name: 'upload',
                query: this.$route.query
            };
        },
    }
};
</script>
  
<style lang="less" scoped>
@import url('../tools/common.less');

.box.selected {
    border: 7px solid blue;
    margin: 2px;
}

.list_item {
    width: 100%;
    height: 200px !important;
    display: flex;
    justify-content: flex-start;
    align-content: flex-start;
    flex-wrap: wrap;
    overflow-x: hidden;
    overflow-y: auto !important;

    /* Add the following styles for the scrollbar */
    scrollbar-width: thin !important;
    scrollbar-color: #345CA0 transparent;

    &::-webkit-scrollbar {
        width: 8px !important;
    }

    &::-webkit-scrollbar-track {
        background-color: transparent;
    }

    &::-webkit-scrollbar-thumb {
        background-color: #345CA0;
        border-radius: 4px;
    }

    .nodata {
        width: 100%;
        height: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .box {
        width: 170px;
        height: 175px;
        background: #FFFFFF;
        border-radius: 12px;
        margin: 10px;
        padding: 15px;
        box-shadow: 0px 3px 7px 0px rgba(0, 0, 0, 0.0500);

        &.selected {
            border: 7px solid blue;
            margin: 2px;
        }

        .main {
            width: 50px;
            height: 100px;
            background: #F5F5F7;
            border-radius: 8px;
            transition: all 0.3s;
            cursor: pointer;

            .cover {
                width: 150px;
                height: 200px;

                img.row {
                    width: 100%;
                    border-radius: 8px;
                }

                img.col {
                    height: 85%;
                    border-radius: 8px;
                }
            }
        }

        .title {
            text-align: left;
            font-weight: 600;
            font-size: 14px;
            color: #000000;
            margin-top: 15px;
            position: relative;

            p {
                line-height: 25px;
            }

            p:last-child {
                font-weight: 400;
                font-size: 12px;
                color: #86868B;
            }

            .action {
                position: absolute;
                left: 50%;
                top: 50%;
                transform: translateX(-50%) translateY(-50%);
                -webkit-transform: translateX(-50%) translateY(-50%);
                transition: all ease 0.35s;
                width: 100%;
                z-index: 2;
                text-align: center;
                opacity: 0;

                i {
                    font-size: 14px;
                    color: #000000;
                    cursor: pointer;
                    border-radius: 50%;
                    padding: 8px;
                    margin: 0px 10px;
                    background-color: #FFFFFF;
                    box-shadow: 0px 12px 16px -4px rgba(16, 24, 40, 0.08), 0px 4px 6px -2px rgba(16, 24, 40, 0.03);
                }
            }

            &:before {
                background: #FFFFFF;
                content: '';
                height: 100%;
                left: 0;
                opacity: 0;
                position: absolute;
                top: 0;
                transition: all ease 0.35s;
                width: 100%;
            }
        }

        &:hover {
            .title .action {
                opacity: 1;
            }

            .title:before {
                opacity: 0.9;
            }
        }
    }
}

.login {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #355CA1;

    .signin {
        width: 892px;
        margin: 50px auto 0px auto;
        background-color: #FFFFFF;
        border-radius: 24px;
        padding: 30px;

        p {
            color: #86868B;
            text-align: center;
        }

        .form {
            margin: 15px 0px;
        }

        label {
            margin: 0px 0px 0px 0px;
            display: block;
        }

        .submit {
            width: 100%;
            background-color: #345CA0;
        }

        span {
            color: #345CA0;
            cursor: pointer;
        }
    }
}
</style>
